/* CSS Variables */
:root {
  --primary-color: #b90415;
  --primary-color-hover: #d3071b;
  --secondary-color: #103063;
  --secondary-color-hover: #143f85;
  --light-color: #f4f4f4;
}

body {
    background: linear-gradient(rgba(30, 27, 38, 0.95), rgba(30, 27, 38, 0.95)),
      url("https://i.ibb.co/FDGqCmM/papers-co-ag74-interstellar-wide-space-film-movie-art-33-iphone6-wallpaper.jpg");
    background-position: center;
    background-size: cover;
  
  }
/* Section */
.section {
  padding: 2rem 0;
}

.section-head {
  font-size: 2.5rem;
  margin: 0;
}

.text-center {
  text-align: center;
}

.img-center {
  text-align: center;
  position: relative;
  image-resolution: center;
}

section#entertainment {
  background: no-repeat bottom/cover;
  padding: 10rem 0;
}

.gift-cards {
  display: grid;
  grid-gap: 20px;
  grid-template-columns: repeat(2, 1fr);
  text-align: left;
}

/* Showcase */
#showcase {
  margin: 0;
  padding: 0;
  background: linear-gradient(rgba(30, 27, 38, 0.95), rgba(30, 27, 38, 0.95));
background-position: center;
background-size: cover;
  width: 100%;
  height: 100vh;
  position: relative;
  overflow-y: hidden;
}

#showcase .container {
  margin-top: 25vh;
}

#showcase h1 {
  font-size: 4rem;
  margin-bottom: 0;
}

#showcase h2 {
  font-size: 2rem;
}

/* Footer */
footer .footer-cols {
  display: grid;
  grid-gap: 20px;
  grid-template-columns: repeat(4, 1fr);
  padding: 2rem;
  text-align: left;
  font-size: 14px;
}

footer .footer-cols ul {
  list-style: none;
}

footer .footer-cols ul li:first-child {
  font-size: 1.2rem;
  padding-bottom: 0.5rem;
  border-bottom: #444 solid 1px;
  margin-bottom: 1rem;
}

footer .footer-bottom {
  background: #333;
  padding: 1rem;
}

/* Utility Classes */

/* change the link color */
.navbar-custom {
  background-color: #15131b;
}
/* change the brand and text color */
.navbar-custom .navbar-brand,
.navbar-custom .navbar-text {
  color: #fe4141;
}
/* change the link color */
.navbar-custom .navbar-nav .nav-link {
  color: #fe4141;
}
/* change the color of active or hovered links */
.navbar-custom .nav-item.active .nav-link,
.navbar-custom .nav-item:hover .nav-link {
  color: red;
}

.lead {
  font-size: 1.3rem;
}

.text-center {
  text-align: center;
}

.margin-bottom {
  margin-bottom: 10px;
  top: 30px;
  position: relative;
}

/* Buttons */

.btn-primary {
  background: #b90415;
  background: var(--primary-color);
}

.btn-primary:hover {
  background: #d3071b;
  background: var(--primary-color-hover);
}

.btn-secondary {
  background: #103063;
  background: var(--secondary-color);
}

.btn-secondary:hover {
  background: #143f85;
  background: var(--secondary-color-hover);
}

/* Text colors */
.text-primary {
  color: #b90415;
  color: var(--primary-color);
}

.text-secondary {
  color: #103063;
  color: var(--secondary-color);
}

.text-light {
  color: #f4f4f4;
  color: var(--light-color);
}

.bg-light {
  background: #f4f4f4;
  background: var(--light-color);
  color: #333;
}

.bg-grey {
  background-color: #1e1b26;
  text-align: center;
}


.mb {
  margin-bottom: 1rem;
}

.mt {
  margin-top: 1rem;
}

/* Navigation */


.cf:before,
.cf:after {
  content: '';
  display: table;
}

.cf:after {
  clear: both;
}

.cf {
  zoom: 1;
}

