/*notification*/
.popup {
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  width: 70vw;
  height: 50vh;
  bottom: 20px;
  right: 21px;
  top: 106px;
  z-index: 2;
  visibility: hidden;
  opacity: 0;
  overflow: hiden;
  transition: 0.64s ease-in-out;
}
.popup-inner {
  border-top-right-radius: 42px;
  border-bottom-right-radius: 42px;
  position: relative;
  bottom: -0.01vw;
  right: -0.01vh;
  display: flex;
  align-items: center;
  max-width: 520px;
  max-height: 388px;
  width: 50%;
  height: 100%;
  background-color: #1e1b26;
  transform: rotate(32deg);
  transition: 0.64s ease-in-out;
}
.popup__photo {
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
  width: 40%;
  height: 100%;
  overflow: hidden;
}
.popup__photo img {
  width: auto;
  height: 100%;
}
.popup__text {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 100%;
  height: 100%;
  padding: 4rem;
}
.popup__text h1 {
  font-size: 2rem;
  font-weight: 600;
  margin-bottom: 2rem;
  text-transform: uppercase;
  color: #fe4141;
}
.popup__text p {
  font-size: 0.875rem;
  color: #fe4141;
  line-height: 1.5;
}
.popup:target {
  visibility: visible;
  opacity: 1;
}
.popup:target .popup-inner {
  bottom: 0;
  right: 0;
  transform: rotate(0);
}
.popup__close {
  position: absolute;
  right: -1rem;
  top: -1rem;
  width: 3rem;
  height: 3rem;
  font-size: 0.875rem;
  font-weight: 300;
  border-radius: 100%;
  background-color: #0a0a0a;
  z-index: 4;
  color: #fff;
  line-height: 3rem;
  text-align: center;
  cursor: pointer;
  text-decoration: none;
}

