}

.col5 {
  width: 17%;
  font-weight: 400;
}

.col6 {
  width: 13.6666666667%;
  font-weight: 400;
}

/* Columns match with their individual number. E.G. col3+col3rest=full width row */
.col3rest {
  width: 63.6666666667%;
  font-weight: 400;
}

.col4rest {
  width: 72%;
  font-weight: 400;
}

.col5rest {
